.card {
  width: 200px;
  background-color: $white;
  border: 1px solid $border--card--color;
  border-radius: 5px;
  padding: 32px 16px 16px;
  box-sizing: border-box;
  transition: $transform-transition;

  &:hover {
    transform: $transform-scale;
  }

  &__photo {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    margin: 0;
    margin-bottom: 0;
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    color: $dark--blue;

    &:hover {
      color: $blue--color;
    }
  }

  &__code {
    font-weight: 400;
    line-height: 14px;
    margin: 4px 0 16px;
    color: $dark--grey;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__review-count {
    margin: 0;
    line-height: 16px;
    color: $dark--blue;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__price-label {
    font-size: 12px;
    line-height: 18px;
    color: $dark--grey;
  }

  &__price-value {
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
    color: $dark--blue;
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;

    box-sizing: border-box;
    height: 40px;
    border: 1px solid $light--blue;
    border-radius: 5px;

    font-size: 14px;
    line-height: 16px;
    color: $white;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;

    background-color: $light--blue;

    transition:
      #{$background-color-transition},
      #{$color-transition};

    &:hover {
      color: $light--blue;
      background-color: $white;
    }
  }
}
